.form-group {
  position: relative;
  max-width: 100px;

  &__label {
    position: absolute;
    background-color: #fff;
    right: 0;
    font-family: "Roboto", sans-serif;
    font-size: 10px;
    font-weight: 700;
    top: -7px;
    display: inline-block;
    left: 6px;
    width: max-content;
    margin: 0;
    padding: 0 5px;
  }

  &__input {
    border-radius: 5px;
    border: 1px solid $gray-2;
    font-size: 10px;
    height: 20px;
    padding: 0 10px;
    transition: 0.3s ease-in-out;
    background-color: white;

    &:hover,
    &:focus,
    &:active {
      border-color: $blue-2;
      background-color: white;
    }
  }

  &--special {
    .form-group__label {
      &:after {
        position: absolute;
        content: "";
        background-color: $green-3;
        width: 5px;
        height: 3px;
        right: -2px;
        top: -3px;
        border-radius: 50%;
      }
    }
  }

  .warning {
    border-color: red;
  }
}
